h3 {
  color: #ffffff;
}


#projects {
  padding: 10px 0;
}

#projects h2 {
  margin-bottom: 20px;
}

#projects .contents {
  display: grid;
  grid-gap: 15px;
  color: #000000;
  grid-template-columns: 1fr;
}

#projects .contents .product {
  position: relative;
  margin-bottom: 15px;
  overflow: hidden;
  border-radius: 10px;
  height: 280px; /* Adjust the height as needed */
}

#projects .contents .product img {
  width: 100%;
  height: 100%;
  transition: all 0.4s ease-in-out;
}

#projects .contents .product .text {
  top: 0;
  left: 0;
  opacity: 0;
  width: 100%;
  height: 100%;
  display: flex;
  color: #ffffff;
  position: absolute;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  transition: opacity 0.4s ease-in-out;
  background-color: rgba(0, 0, 0, 0.6);
}

#projects .contents .product h3 {
  font-size: 24px;
  text-align: center;
  margin-bottom: 10px;
  white-space: break-spaces;
}

#projects .contents .product a {
  font-size: 24px;
  display: block;
  color: #ffffff;
  margin-bottom: 10px;
  text-decoration: none;
}

#projects .contents .product:hover .text,
#projects .contents .product:focus .text {
  opacity: 1;
}

#projects .contents .product:hover img,
#projects .contents .product:focus img {
  transform: scale(1.3);
}

@media (min-width: 600px) {
  #projects .contents {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 800px) {
  #projects {
    padding: 40px 0;
  }

  #projects h2 {
    margin-bottom: 50px;
  }

  #projects .contents {
    grid-template-columns: repeat(3, 1fr);
  }
}


